import React from "react";
import { Card, Col, Row } from "react-bootstrap";
import { DatePicker } from "antd";
import { MetaTags, PageHeader } from "../../../components";

function Dashboard() {
  const { RangePicker } = DatePicker;
  return (
    <>
      <MetaTags title="Dashboard" />
      <div className="nk-block-head nk-block-head-sm ">
        <div className="nk-block-between">
          <Card className="w-100 rounded-2 mb-4">
            <Card.Body>
              <PageHeader heading="Dashboard" />
            </Card.Body>
          </Card>
        </div>
        <Row>
          <Col md={8}>
            <Card>
              <Card.Body>
                <div className="dashboardData">
                  <div className="d-flex align-items-center flex-wrap justify-content-center mb-4">
                    <div className="badge badge-1 mb-2">
                      <p className="mb-0">Total cutomers 8336</p>
                    </div>
                    <div className="badge badge-2 mb-2">
                      <p className="mb-0">Current month 31</p>
                    </div>
                    <div className="badge badge-3 mb-2">
                      <p className="mb-0">Current Week 10</p>
                    </div>
                    <div className="badge badge-4 mb-2">
                      <p className="mb-0">Todays customers 0</p>
                    </div>
                  </div>

                  <div className="d-flex flex-wrap align-items-center justify-content-center">
                    <div className="userCount userCount-1 d-flex align-items-center mb-2">
                      <em className="icon ni ni-android" />
                      <a href="#!" className="d-inline-block">
                        6245
                      </a>
                    </div>
                    <div className="userCount userCount-2 d-flex align-items-center mb-2">
                      <em className="icon ni ni-apple" />
                      <a href="#!" className="d-inline-block">
                        1023
                      </a>
                    </div>
                    <div className="userCount userCount-3 d-flex align-items-center mb-2">
                      <em className="icon ni ni-live" />
                      <a href="#!" className="d-inline-block">
                        347
                      </a>
                    </div>
                    <div className="userCount userCount-4 d-flex align-items-center mb-2">
                      <em className="icon ni ni-monitor" />
                      <a href="#!" className="d-inline-block">
                        721
                      </a>
                    </div>
                    <div className="userCount userCount-5 d-flex align-items-center mb-2">
                      <p className="mb-0">Total Cutomers</p>
                      <a href="#!" className="d-inline-block">
                        8336
                      </a>
                    </div>
                  </div>
                </div>
              </Card.Body>
            </Card>
          </Col>
          <Col md={4}>
            <Card>
              <Card.Body className="p-3">
                <div>
                  <form>
                    <div className="d-flex align-items-end">
                      <div className="form-group mb-0 w-100">
                        <label className="form-label" htmlFor="datepicker">
                          Date Range
                        </label>
                        <RangePicker className="form-control form-control-lg d-flex" />
                      </div>
                      <button className="btn btn-primary ms-3"> Search</button>
                    </div>
                  </form>
                </div>
              </Card.Body>
            </Card>
            <Card>
              <Card.Body className="p-4">
                <div className="cardRight d-flex align-items-center justify-content-between">
                  <p className="text-blue mb-0">Total Revenue</p>
                  <h3 className="mb-0">45,645.00</h3>
                </div>
              </Card.Body>
            </Card>
            <Card>
              <Card.Body className="p-4">
                <div className="cardRight d-flex align-items-center justify-content-between">
                  <p className="text-green mb-0">Total Orders</p>
                  <h3 className="mb-0">344</h3>
                </div>
              </Card.Body>
            </Card>
            <Card>
              <Card.Body className="p-4">
                <div className="cardRight d-flex align-items-center justify-content-between">
                  <p className="mb-0">Sample for Tomorrow</p>
                  <h3 className="mb-0">0</h3>
                </div>
              </Card.Body>
            </Card>
            <Card>
              <Card.Body className="p-4">
                <div className="cardRight d-flex align-items-center justify-content-between">
                  <p className="text-purple mb-0">Average Order Value</p>
                  <h3 className="mb-0">132.69</h3>
                </div>
              </Card.Body>
            </Card>
            <Card>
              <Card.Body className="p-4">
                <div className="cardRight d-flex align-items-center justify-content-between">
                  <p className="text-green mb-0">
                    Average Order per Delivery Executive
                  </p>
                  <h3 className="mb-0">17.2</h3>
                </div>
              </Card.Body>
            </Card>
            <Card>
              <Card.Body className="p-4">
                <div className="cardRight d-flex align-items-center justify-content-between">
                  <p className="text-black mb-0">Order Report</p>
                  <button className="btn btn-primary">View</button>
                </div>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </div>
    </>
  );
}

export default Dashboard;
